import {GPGPUProgram} from './gpgpu_math';
import {getCoordsDataType} from './shader_compiler';

export class StridedSliceProgram implements GPGPUProgram {
  variableNames = ['x'];
  outputShape: number[];
  userCode: string;
  rank: number;

  constructor(begin: number[], strides: number[], shape: number[]) {
    this.outputShape = shape;
    this.rank = shape.length;
    const dtype = getCoordsDataType(this.rank);

    let newCoords = '';
    if (this.rank === 1) {
      newCoords = 'coords * strides + begin';
    } else {
      newCoords =
          shape.map((_, i) => `coords[${i}] * strides[${i}] + begin[${i}]`)
              .join(',');
    }

    this.userCode = `
      ${dtype} begin = ${dtype}(${begin});
      ${dtype} strides = ${dtype}(${strides});

      void main() {
        ${dtype} coords = getOutputCoords();
        setOutput(getX(${newCoords}));
      }
    `;
  }
}
